<template>
<ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <!-- eslint-disable-next-line vuejs-accessibility/click-events-have-key-events -->
            <a href="#" @click.prevent="backPage"
            :class="{ 'pagination__link--disabled' : 1 === this.page }"
            class="pagination__link pagination__link--arrow"
            aria-label="Предыдущая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" @click.prevent="paginate(pageNumber)" class="pagination__link"
            :class="{
              'pagination__link--current' : pageNumber === page,
              }">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <a @click.prevent="nextPage"
            :class="{ 'pagination__link--disabled' : pages === this.page }"
            class="pagination__link pagination__link--arrow" href="#"
            aria-label="Следующая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
export default {
  props: ['page', 'count', 'perPage'],
  model: {
    prop: ['page'],
  },
  methods: {
    acPage() {
      return this.page;
    },
    paginate(page) {
      this.$emit('input', page);
    },
    nextPage() {
      const pages = Math.ceil(this.count / this.perPage);
      if (pages !== this.page) {
        this.$emit('input', this.page + 1);
      }
    },
    backPage() {
      if (this.page !== 1) {
        this.$emit('input', this.page - 1);
      }
    },
  },
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
};
</script>
