<template>
  <router-link class="header__cart" :to="{ name: 'cart' }" aria-label="Корзина с товарами">
    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label="Количество товаров">{{ cartCount }}</span>
    <!-- <span class="header__count" aria-label="Количество товаров">{{ $store.state.cartProducts.length }}</span> -->
    <!-- <div v-show="addLoadC">Load</div>
    <div v-show="addErrorC">Error</div> -->
  </router-link>
</template>

<script>
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['addLoad', 'addError']),
    // addLoadC() {
    //   return this.addLoad;
    // },
    // addErrorC() {
    //   return this.addError;
    // },
    cartCount() {
      let status = this.$store.state.cartProducts.length;

      if (this.addLoad) {
        status = '+';
      } else if (this.addError) {
        status = 'Error';
      }
      return status;
    },
  },
};
</script>
